import React, { Component } from 'react';
import { connect } from 'react-redux';
import { app } from '../../config/app';
import Grid from '../../utility/grid';
import Switchtoggle from '../../components/form/switchtoggle';
import Table from '../../components/grid/Table';
import NoRecord from '../../components/grid/NoRecord';
import { FONTS, FONT, FONT_DELETE, FONT_STATUS_UPDATE } from "../../utility/constants";
import Form from '../../utility/Form';
import { save, uploadFontFile } from '../../actions/fontfamily';
import Forms, { initialState, validation } from "./form";
import toastr from 'toastr';

class Fontfamily extends Component {

    constructor(props) {
        super(props);
        this.state = {
            openPopup : false,
            isEdit : false
        }
        this.displayRows = this.displayRows.bind(this);
        this.state = initialState();
      //  this.initialState = initialState();
        const formValidation = validation();
        this.grid = Grid.getInstance({ currentComponent: this, url: 'getListsWithPagination', actionType: FONTS, method: 'POST' });

        this.font = Form.getInstance(
            this,formValidation,
            (fields) => {
                this.props.dispatch(save(fields));
                this.resetForm();
            },
            (views) => {
                this.props.dispatch(uploadFontFile(views, 'font_file', 'fileUpload'))
            },
            {}
        );   
    }
    initialState(){
        return {
            filters: {
                domain_id: app.DOMAIN_ID,
                searchVal: '',
                modelName: 'promize_fonts',
                searchKey: 'font_name',
                idKey: 'font_id'
            },
            fonts: [],
        }
    }

    componentDidMount() {
        this.grid.getRecords();
    }
    resetForm(){
        let fields = {
            font_name : '',
            font_code : '',
            font_file : '',
            domain_id : app.DOMAIN_ID,
            status : 1
        }
        this.setState({fields},()=>{
            this.grid.getRecords();
        })
    }

    setData(props) {
        const response = props.fonts.response;
        const fontResponse = props.font.response;
        if (response !== this.props.fonts.response) {
            if (!props.fonts.isFetching && response.data && Object.keys(response.data).length > 0 && response.status && response.status === 200) {
                this.setState({ fonts: response.data || [], openPopup : false});
            }
            else if (!props.fonts.isFetching && props.fonts.isError) {
                this.setState({ fonts: [] });
            }
        }
        if (fontResponse !== this.props.font.response) {
            if (!props.font.isFetching && !props.fontSize.isError  && response.data && Object.keys(response.data).length > 0 &&response.status && response.status === 200) {
                toastr.success(props.font.response.message)
                this.setState({ openPopup : false },()=>{
                    this.resetForm()
                });
            }
            else if (!props.font.isFetching && props.font.isError) {
                toastr.error(props.font.response.message)
            }
        }
    }
    editFont(e, font){
        e.preventDefault();
        this.setState({fields : font,openPopup : !this.state.openPopup, isEdit : true});
    }
    displayRows() {
        const fonts = this.state.fonts;
        let fontRows = <NoRecord isFetching={this.props.fonts.isFetching} colspan="10" />;
        if (fonts.length > 0) {
            fontRows = fonts.map((font, index) => {
                var status = (font.status == 1) ? true : false;
                //var clipart_category_image = clipart.
                return <tr key={index}>
                    <td>{font.font_name}</td>
                    <td>
                        <Switchtoggle  {...this.props} 
                            value={status}
                            fieldName={'status'}
                            id={font.font_id}
                            onChange={(e) => { this.grid.statusUpdate({ url: "changeStatus", actionType: FONT_STATUS_UPDATE, params: font, modelName: "promize_fonts",
                            PKey: "font_id" }, e) }}
                            spanLabel={{ first: 'Active', second: 'InActive' }}
                            checkValue={{ first: true, second: false }}
                        />
                    </td>
                    <td>
                        <div className="table-actions">
                            <button className="btn-tbl edit" onClick={(e) => this.editFont(e,font)}><i className="fa fa-pencil-alt"></i></button>
                            <button className="btn-tbl delete" onClick={(e) => this.table.delete.handleToogleModal({ id: font.font_id, idKey: 'ids', url: 'deleteRecord', actionType: FONT_DELETE, modelName: "promize_fonts", PKey: "font_id" }, e)}><i className="fa fa-trash-alt"></i></button>
                        </div>
                    </td>
                </tr>
            })
        }
        return fontRows;
    }
    componentWillReceiveProps(nextProps) {
        this.setData(nextProps);
    }
    enablePopup = () =>{
        let fields = {
            font_name : '',
            font_code : '',
            font_file : '',
            domain_id : app.DOMAIN_ID,
            status : 1
        }
        this.setState({fields, openPopup : !this.state.openPopup, isEdit : false})
    }
    render() {
        return (
            <React.Fragment>              
                {this.state.openPopup && <div>
                       <Forms {...this} font={this.font} title={this.state.isEdit ? "Edit Font" : "Add Font"} enablePopup={this.enablePopup} currentComponent = {this}/>
                    </div>
                    }
            <Table
                ref={instance => { this.table = instance; }}
                {...this.props} {...this.state}
                pageTitle="Font Family"
                managementType={FONT}
                addButton={
                    <button className="btn btn-promize-primary" onClick={this.enablePopup}>
                        <span>{"Add"}</span>
                    </button>
                }
                grid={this.grid}
                data={this.props.fonts}
                rowDelete={this.props.fontDelete}
            >
                <table className="promize-table table-hover table-action" id="fixTable">
                    <thead>
                        <tr>
                            <th className={(this.grid.sortType) ? ((this.grid.sortType == 'ASC') ? "sorting-asec" : "sorting-desc") : "sorting"} onClick={(e) => this.grid.handleColSorting(e)}>Name</th>
                            <th>Status</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.displayRows()}
                    </tbody>
                </table>
            </Table>
            </React.Fragment>)

    }
}

function mapStateToProps(state) {
    return {
        fontStatus: state.fontStatus,
        fontDelete: state.fontDelete,
        fileUpload : state.fileUpload,
        fonts: state.fonts,
        font: state.font,
    };
}

export default connect(mapStateToProps)(Fontfamily);
