import { FONT, FONTS, PENDING,FILE_UPLOAD, FULFILLED, REJECTED } from "../../utility/constants";
import { app } from "../../config/app";

export function save(data) {
    let method = 'POST', url = "createRecord";
    data = {
        ...data,
        modelName: "promize_fonts",
        idKey: "font_id"
    }
    if (data.font_id) {
        method = 'PUT';
        url = 'updateRecord'
        delete data.created_date;
        delete data.updated_date;
    }
    return {
        types: [`${FONT}_${PENDING}`, `${FONT}_${FULFILLED}`, `${FONT}_${REJECTED}`],
        method: method,
        url: url,
        data: data,
        payload: { receiveType: 'postData' }
    }
}

export function uploadFontFile(file, typeName, name) {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('name', typeName);
    return {
        types: [`${FILE_UPLOAD}_${PENDING}`, `${FILE_UPLOAD}_${FULFILLED}`, `${FILE_UPLOAD}_${REJECTED}`],
        method: 'POST',
        requestBodyType: 'FormData',
        url: 'uploadFontFile',
        data: formData,
        payload: { receiveType: 'postData' }
    }
}

export function getAllFonts() {
    let data = {
        modelName: "promize_fonts",
        idKey: "font_id",
        domain_id: app.DOMAIN_ID
    }
    const request = {
        types: [`${FONTS}_${PENDING}`, `${FONTS}_${FULFILLED}`, `${FONTS}_${REJECTED}`],
        method: 'POST',
        url: 'getAllRecords',
        data: data,
    }

    return request;
}