import React, { Component } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import Input from '../../components/form/input';
import FileUpload from '../../components/files/FileUpload';
import { SubmitButton } from "../../components/form/SubmitButton";
import { app } from '../../config/app';

export function initialState () {
     const initialState = {
        filters: {
            domain_id: app.DOMAIN_ID,
            searchVal: '',
            modelName: 'promize_fonts',
            searchKey: 'font_name',
            idKey: 'font_id'
        },
        fields :{
            font_name : '',
            font_code : '',
            font_file : '',
            domain_id : app.DOMAIN_ID,
            status : 1
        },
        fonts: [],
        inputErrors:{},
    }

    return initialState;
}

export const validation = (props) => {
    const rules = {
        "font_name":"required",
        "font_file" : "required"
    }
    return rules;
}
class Forms extends Component {
    constructor(props){
        super(props);
    }
    render() {
        return <div className="promize-modal-cover">
        <div className="promize-modal modal-sm">
            <div className="promize-modal-header">
                <h3 className="modal-title">
                    <span>
                        {this.props.title}
                    </span>
                </h3>
            </div>
            <div className="promize-modal-body">
              <form onSubmit={this.props.font.handleSubmit}  method="POST">
                <div className="df-wrap">
                    <div className="wd-100">
                        <Input {...this.props} form ={this.props.font}
                            fieldName = {'font_name'}
                            value={this.props.state.fields.font_name}
                            labelName={"Font Name"}
                            validation={false}
                        /> 
                    </div>
                    <div className="wd-100">
                        <Input {...this.props} form ={this.props.font}
                            fieldName = {'font_code'}
                            value={this.props.state.fields.font_code}
                            labelName={"Font Code"}
                            validation={false}
                        /> 
                    </div>
                    <div className="wd-100">
                        <FileUpload {...this.props.state} form ={this.props.font} currentComponent={this.props.currentComponent}
                            labelName={"Font File"}
                            fieldName={'font_file'}
                            dataName={'font_file'}
                            validation={false}
                            value = {this.props.state.fields.font_file}
                            error = {this.props.state.inputErrors}
                        /> 
                    </div>
                    <div className="mt-20 wd-100 df df-end">
                        <button  className="btn btn-promize-secondary" onClick={this.props.enablePopup}>Cancel</button>
                        <SubmitButton className="btn btn-promize-primary" text={"Save"} response={this.props.state.fields} />  
                    </div>
                </div>       
            </form>
            </div>
        </div>
    </div>
    }
}
function mapStateToProps(state) {
    return {
        fileUpload : state.fileUpload,
        fonts: state.fonts,
    };
}


export default connect(mapStateToProps)(Forms);