import React, { Component } from 'react';
import { connect } from 'react-redux';
import Dropzone from 'react-dropzone'

class FileUpload extends Component {

    constructor(props) {
        super(props);
        this.change = this.change.bind(this);
        this.onDrop = this.onDrop.bind(this);
        this.onDragOver = this.onDragOver.bind(this);
        this.onDragLeave = this.onDragLeave.bind(this);
    }

    change(e) {
        var str = e.target.value;
        let filename = str.split('\\');
        let length = filename.length -1;
        filename = filename[length];
        let {fields, inputErrors} = this.props;
        fields = {
            ...fields,
            font_file : filename
        }
        inputErrors = {}
        this.props.currentComponent.setState({fields, inputErrors})
        this.props.form.handleFileSelect(e);
    }

    onDrop(acceptedFiles) {
        this.props.form.handleFileDrop({ "files": acceptedFiles }, "font_file")
        this.setState({ highlight: false })
    }

    onDragOver(e) {
        this.setState({ highlight: true })
    }

    onDragLeave(e) {
        this.setState({ highlight: false })
    }

    render() {
        let error = this.props.error[this.props.fieldName] ? true : false ;
        return (<Dropzone onDrop={acceptedFiles => this.props.form.handleFileDrop({ "files": acceptedFiles }, "image_file")}>
            {({ getRootProps, getInputProps }) => (
                <React.Fragment>
                <div className="promize-upload" {...getRootProps()}>
                    <div className="promize-upload-header">
                        <label className="promize-upload-title">{this.props.labelName}</label>
                        <div className="custom-upload-product">
                            <div className="promize-upload-button">
                                <input {...getInputProps()} type="file" name="image_file" id="image_file" onChange={this.change} accept=".ttf" />
                                <span className="file-custom">Upload File</span>
                            </div>
                        </div>
                    </div>
                    <div className="promize-upload-content">
                        <div className="promize-content-before-upload">
                            <p>{this.props.value ? this.props.value  : <><i className="fa fa-upload mr-15"></i>Browse the file</>}</p>
                        </div>
                    </div>
                    {error && <span className="promize-form-error">{`${this.props.labelName} ` + "Required"}</span> }    
                </div>
                </React.Fragment>
            )}
        </Dropzone>
        );
    }
}

function mapStateToProps(state) {
    const { image } = state;
    return {
        image
    };
}

export default connect(mapStateToProps)(FileUpload);
